.mgs-container {
    background: url(../assets/images/background.png);
    color: #fff;
    padding: 20px;
    span {
        font-size: 1.7rem;
    }
    .logo-user {
        margin: auto;
    }
    .photo-user {
        width: 60px;
        padding: 10px;
        background: linear-gradient(90deg, #fd997a 0%, #901473 100%);
        border-radius: 40px;
        overflow: hidden;
        height: 120px;
        .photo-images {
            background-image: url(https://media-exp1.licdn.com/dms/image/C4E03AQHiHv-1a9PV4A/profile-displayphoto-shrink_200_200/0?e=1609372800&v=beta&t=8kS9fQs7GSdTWZZ5tkVzCnXUubze3qQrQQklx6sVetE);
            background-position: center;
            background-size: cover;
            border-radius: 30px;
            height: 100%;
        }
    }
}