.container-meet {
    max-width: calc(100% - 800px);
    margin: 20px;
}

.bar-meet {
    background: #fff;
    padding: 10px 30px;
    border-radius: 30px;
    max-height: 40px;
    max-width: calc(100% - 40px);
    margin: 0px 20px;
    .bar-meet-container {
        position: relative;
        top: -5px;
        background: #fff;
        padding: 10px;
        border-radius: 40px;
        .bar-meet-ico {
          background: #11162a;
          border-radius: 35px;
          height: 80px;
          margin: auto;     
          border-radius: 25px;
        }
    }
}

.logo-user {
    border: solid 2px #2dff7b;
    border-radius: 20px;
    border-radius: 20px;
    width: 230px;
    height: 60px;
}

.meet-center {
  height: calc(100vh - 280px);
}

.meet-seccion {
  padding: 20px 15px;
  height: calc(100vh - 320px);
}

.video-frame {
    border: solid #42fded 2px;
    padding: 10px;
    border-radius: 10px;
}

.reasons-figure {
    position: fixed;
    z-index: -999;
}

.reasons-figure-1 {
    top: 21%;
    left: 56%;
    width: 12.5%;
}

.reasons-figure-3 {    
    top: 22%;
    left: 13%;
    width: 12%;
}

.user-name {
  text-align: right;
  .user-photo {
    position: relative;
    top: -5px;
    background: #fff;
    padding: 10px;
    border-radius: 40px;
    .user-img {
      border-radius: 35px;
      background-image: url(https://media-exp1.licdn.com/dms/image/C4E03AQHiHv-1a9PV4A/profile-displayphoto-shrink_200_200/0?e=1609372800&v=beta&t=8kS9fQs7GSdTWZZ5tkVzCnXUubze3qQrQQklx6sVetE);
      background-position: center;
      background-size: cover;
      width: 100%;
      height: 80px;
      border-radius: 25px;
    }
  }
}
