@font-face {
  font-family: 'Eventograma';
  src: local('Eventograma'), url(./assets/fonts/GOTHIC.TTF);
}

a {
  text-decoration: none;
}

h1 {
  color: #fff;
  font-size: 3.5em;
}

hr {
  margin: 0px;
  border: solid #42fded 1px;
}

fieldset {
  display: none;
}

video {
  width: 100%;
}

body, html {
  font-family: 'Eventograma';
  margin: 0px;
  padding: 0px;
}

.app-frame {
  padding: 25px 0px;
}

.background-image {
  background-image: url(./assets/images/jesus-loves-austin-0O0gux4OZX4-unsplash.jpg);
  background-size: cover;
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.border {
  background: #000;
  border-radius: 10px;
}

.box-shadow {
  box-shadow: 0px 130px 61px -49px rgba(0,0,0,0.47) !important;
}

.event-logo {
  background: linear-gradient(90deg, #2dff7b 0%, #45fdfe 100%) !important;
  font-size: 1.5rem !important;
  margin: 25px auto;
  width: 224px;
  height: 32px;
  border-radius: 20px;
  color: #fff;
  padding: 20px !important;
}

.icon-social {
  width: 32px;
  height: 32px;
}

.MuiCardActions-root {
  padding: 16px !important;
}

.MuiPaper-root {
  border-radius: 10px;
}

.MuiPaper-rounded {
  border-radius: 15px !important;
  overflow: hidden;
}

.MuiLinearProgress-colorPrimary {
  background-color: #ffff !important;
}

.MuiLinearProgress-barColorPrimary {
  background: linear-gradient(90deg, #2dff7b 0%, #45fdfe 100%) !important;
}

.MuiTextField-root {
  width: 100%;
}

.MuiFormControl-root {
  width: 100%;
}

.MuiOutlinedInput-root {
  background: #e6e6e6;
  border: 0px !important;
  border-radius: 48px !important;
}

.MuiOutlinedInput-input{ 
  padding: 18.5px 15px !important;
}

.MuiButton-contained {
  border-radius: 48px !important;
  background: #2dff78 !important;
  color: #ffff !important;
}

.MuiAutocomplete-root {
  width: 100% !important;
}

.MuiBox-root-2 {
  padding: 0px 8px !important;
}

.MuiBox-root-6 {
  padding: 0px 8px !important;
}

.MuiTabs-flexContainer {
  border-bottom: solid 3px #ccc;
  padding: 0px 0px;
  font-weight: bold;  
  [aria-selected="true"] {
    color: #2dff7b;
  }
}

.login-tabs {
  min-width: 50% !important;
}

.text-align-center {
  text-align: center;
}

.tab-panel > div {
  padding: 20px;
}

.PrivateTabIndicator-colorSecondary-9 {
  padding-top: 5px;
  background: linear-gradient(90deg, #2dff7b 0%, #45fdfe 100%) !important;
}
